body{
    background: linear-gradient(135deg, #f8bbd0,#e1bee7);
    color: #fff;
    font-family: 'Comic Sans MS', cursive, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    overflow: hidden;
}
.calculator{
    background: rgba(255,2555,255,0.8);
    border: 8px solid #ff80ab;
    padding: 30px;
    box-shadow: 0 0 30px rgba(255,128,171, 0.8);
    width: 500px;
    text-align:  center;
    backdrop-filter: blur(10px);
    margin: 20px;
    position: relative;
    overflow: hidden;
}
.display{
    width: 95%;
    height: 80px;
    margin-bottom: 30px;
    padding: 15px;
    font-size: 32px;
    text-align: right;
    border: 2px solid #ff80ab;
    border-radius: 25px;
    background: rgba(255,128,171,0.8);
    color:#bebebe;
    box-shadow: inset 0 0 15px rgba(74, 19, 1, 0.8);
}
.buttons{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 15px;
}

button{
    height: 60px;
    font-size: 18px;
    border: none;
    border-radius: 50px;
    background: linear-gradient(135deg, #ff80ab, #ff4081);
    color: #fff;
    cursor: pointer;
    box-shadow: 0 10px 10px rgba(255,128,171,0.5);
    transition: all 0.3s ease;
    position: relative;
    overflow:hidden;
}
button:hover{
    background: linear-gradient(135deg, #bc2c5a,#e8e5e6);
    box-shadow: 0 0 20px rgb(115, 0, 38);
    transform: scale(1.1);
}
button:active{
    transform: scale(0.95);
    box-shadow: 0 10px 10px rgba(255,128,171,0.5);
}
#backspace{
    background: linear-gradient(135deg, #ffccbc, #ffab91);
    color: #fff;
}
#backspace:hover{
    background: linear-gradient(135deg, #ffab91, #ffccbc);
    box-shadow: 0 0 20px rgba(255,171,145,1);
}
#equals{
    background: linear-gradient(135deg, #c8e6c9, #81c784);
    color: #fff;
}
#equals:hover{
    background: linear-gradient(135deg, #81c784, #c8e6c9);
    box-shadow: 0 0 20px rgba(129, 199, 132, 1);
}
#add,
#subtract,
#multiply,
#divide{
    background: linear-gradient(135deg, #b3e5fc, #4fc3f7);
    color: #fff;
}
#add:hover,
#subtract:hover,
#multiply:hover,
#divide:hover{
    background: linear-gradient(135deg,  #4fc3f7, #b3e5fc);
    box-shadow: 0 0 20px rgba(79,195,247,1);
}

#sin, 
#cos,
#tan,
#log,
#ln,
#pi,
#e{
    background: linear-gradient(135deg, #e70852, #f06292);
    color: FFF;
}

#sin:hover, 
#cos:hover,
#tan:hover,
#log:hover,
#ln:hover,
#pi:hover,
#e{
    background: linear-gradient(135deg,#f06292, #e70852);
    box-shadow: 0 0 20px rgba(244,143,177,1);
}
#clear-history{
    background: linear-gradient(135deg, #ffe0b2, #ffb74d);
    color: #fff;
    border: none;
    border-radius: 50px;
    box-shadow:  0 0 10px rgba(255,183,77,0.8);
    transition: all 0.3s ease;
}

#clear-history:hover{
    background: linear-gradient(135deg, #ffb74d, #ffe0b2);
    box-shadow: 0 0 20px rgba(255,183,77,1);
    transform: scale(1.1);
}

#clear-history:active{
    box-shadow: 0 0 10px rgba(255,183,77,0.8);
    transform: scale(0.95);
}

@media(max-width: 600px){
    .calculator{
        width: 90%;
        padding: 20px;
    }
    .buttons{
        gap: 10px;
    }
    .button{
        height: 60px;
        font-size: 24px;
    }
}

@keyframes bounce{
    0%, 100%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-10px);
    }
}

button{
    animation: bounce 2s infinite ease-in-out;
}

.calculator::before{
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(135deg, rgba(255, 162, 0, 0.5), rgba(0, 17, 255, 0.5));
    animation: rotate-background 10s linear infinite;
    z-index:-1;
}

@keyframes rotate-background{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}